@font-face {
  font-family: "PlusJakartaSans-Medium";
  src: url(../../assets/fonts/static/PlusJakartaSans-Medium.ttf) format("truetype");
  font-weight: normal;
}
@font-face {
  font-family: "PlusJakartaSans-Bold";
  src: url(../../assets/fonts/static/PlusJakartaSans-ExtraBold.ttf) format("truetype");
  font-weight: bold;
}
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.5rem;
  font-family: "PlusJakartaSans-Medium";
  color: #333;
}
@media (min-width: 1440px) {
  body {
    background-color: #f6fbff;
  }
}

@media (min-width: 1440px) {
  .container {
    width: min(50%, 120rem);
    margin: 5rem auto 10rem auto;
  }
}

.active {
  background-color: #f2f8fc;
}
.active .message__dod {
  background-color: #ef5857;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  display: inline-block;
  margin-inline: 5px;
}

button {
  cursor: pointer;
}

*:focus {
  outline: dotted #083078 3px;
}

.notifications {
  background-color: #fff;
}
@media (min-width: 1440px) {
  .notifications {
    padding-inline: 2rem;
    border-radius: 1rem;
    box-shadow: 0px 8px 24px rgba(149, 157, 165, 0.2);
  }
}
.notifications__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-inline: 1rem;
}
.notifications__heading-box {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.notifications__haeding {
  font-weight: bold;
}
.notifications__number-container {
  background-color: #083078;
  color: #fff;
  padding: 2px 5px;
  width: 3.5rem;
  height: 3rem;
  border-radius: 1rem;
  padding-inline: 1px;
  font-size: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.notifications-btn {
  border: none;
  background-color: #fff;
  color: #6b7c8d;
  font-size: 1.5rem;
}

.messages {
  margin-inline: 1rem;
}
@media (min-width: 1440px) {
  .messages {
    padding-bottom: 2rem;
  }
}

.message {
  cursor: pointer;
  border-radius: 1rem;
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
  padding: 1rem;
}
.message__img {
  display: flex;
  align-items: flex-start;
}
.message__img img {
  width: 5rem;
  margin-top: 1.2rem;
}
.message__user {
  font-weight: bold;
}
.message__content-img {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.message__content-img img {
  width: 4.5rem;
  height: 4.5rem;
}
.message__text {
  margin-bottom: 5px;
}
.message__user, .message__info {
  font-family: "PlusJakartaSans-Bold";
}
.message__info-1 {
  color: #606f7f;
}
.message__info-2 {
  color: #083078;
}
.message__time {
  margin-top: 0;
}
.message__box {
  border: 1px solid #dee2e6;
  padding: 1rem;
  color: #6b7c8d;
  border-radius: 5px;
}

.attribution {
  display: flex;
  justify-content: center;
}